<template>
  <v-main>
    <div class="login-page">
      <div class="logo"></div>
      <div class="title">後台管理系統</div>
      <div class="login-container">
        <v-form ref="form" v-model="valid" lazy-validation>
          <v-text-field
            outlined
            v-model="account"
            :rules="rules.account"
            label="帳號"
            required
            clearable
          >
          </v-text-field>
          <v-text-field
            :type="show ? 'text' : 'password'"
            :append-icon="show ? 'mdi-eye' : 'mdi-eye-off'"
            outlined
            v-model="password"
            :rules="rules.pwd"
            label="密碼"
            required
            clearable
            @click:append="show = !show"
          >
          </v-text-field>
          <v-btn class="login-btn" :disabled="!valid" @click="login()"
            >登入</v-btn
          >
        </v-form>
      </div>
    </div>

    <div class="text-center">
      <v-dialog class="field" v-model="checkModal" hide-overlay persistent>
        <v-card
          class="text-center error-card d-flex align-center"
          width="auto"
          height="100"
          style="background-color: #000000; opacity: .9;"
        >
          <v-card-text class="text-h6 white--text pa-0 px-2">
            帳號或密碼有誤
          </v-card-text>
        </v-card>
      </v-dialog>
    </div>
  </v-main>
</template>

<script>
import { login } from "@/api";

export default {
  name: "Login",

  data: () => ({
    valid: true,
    account: null,
    password: null,
    rules: {
      account: [(v) => !!v || "請輸入帳號"],
      pwd: [(v) => !!v || "請輸入密碼"],
    },
    show: false,
    checkModal: false,
  }),

  methods: {
    login() {
      if (this.$refs.form.validate()) {
        const params = new URLSearchParams();
        params.append("id", this.account);
        params.append("password", this.password);

        login(params).then((response) => {
          if (response.data.status_code == "SUCCESS") {
            localStorage.setItem("auth_token", response.data.auth_token);
            this.$router.push("/");
          } else {
            this.checkModal = true;
            setTimeout(() => (this.checkModal = false), 1500);
          }
        });
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.login-page {
  background-image: url("../assets/login_bg.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
  padding-bottom: 30px;
  &::-webkit-scrollbar {
    display: none;
  }
  .logo {
    width: 150px;
    margin: 80px auto 15px auto;
    background-image: url("../assets/login_logo.png");
    background-repeat: no-repeat;
    background-size: 100%;
    background-position: center;
    &::before {
      content: "";
      display: block;
      padding-bottom: 40.8412483%;
    }
  }
  .title {
    margin: 10px 0 20px 0;
    color: #fff;
    text-align: center;
    font-weight: bold;
    font-size: 1.2rem;
    font-family: 微軟正黑體;
  }
  .login-container {
    max-width: 415px;
    width: calc(100% - 30px);
    padding: 15px;
    border-radius: 10px;
    background-color: #fff;
    margin: 0 auto;
    .MuiOutlinedInput-root {
      .MuiOutlinedInput-notchedOutline {
        border-color: #d9d9d9;
      }
      &:focus-within {
        .MuiOutlinedInput-notchedOutline {
          border-color: #3f51b5 !important;
        }
      }
      &:hover {
        .MuiOutlinedInput-notchedOutline {
          border-color: #d9d9d9;
        }
      }
    }
    .input-text {
      position: absolute;
      right: 10px;
      top: 50%;
      transform: translateY(-50%);
    }
    .login-btn {
      height: 50px;
      width: 100%;
      padding: 10px 0;
      background-color: transparent;
      border: 0;
      border-radius: 5px;
      transition: all 0.15s ease-in-out;
      font-weight: bold;
      font-family: 微軟正黑體;
      background-color: rgba(19, 34, 53, 0.9);
      color: #fff;
      &:disabled {
        background-color: #969696;
        color: #666;
        pointer-events: none;
      }
    }
  }
}

.field {
  max-width: 330px;
}
</style>
